.Auth-form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    min-height:100%;
    overflow: hidden;
  }
  
.Auth-form {
    width: 28vw;
    box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
    padding: 30px 8px 30px 8px;
    border-radius: 8px;
    background-color: white;
    border: 0.1px solid #ccc;
    &.signup {
        height: 20.75em;
      }
    &.signin {
        height: 30vw;
    }
}
  
.Auth-form-content {
    margin: 0% 10%;
    padding: 0%;
    height: 100%;
}
  
.Auth-form-title {
    text-align: center;
    font-size: 24px;
    color: #031C30;
    font-weight: 800;
    padding: 0%;
    margin: 0%;
}
  
.text-center{
    font-family: 'Quicksand';
    font-size: 14px;
    text-align: center;
    padding: 0%;
    margin: 0% 0% 8% 0%;
}

.mt-2{
    padding-top: 15px;
    margin: 0%;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
}
  
.link-primary {
    color: #031C30;
    cursor: pointer;
    font-weight: bold;
}
  
.form-group {
    margin-bottom: 15px;
}

input, button {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    box-sizing: border-box; /* Include padding and border in the width calculation */
}
  
input {
    outline: none;
    border: 1px solid #ccc;
    -webkit-transition: 0.5s;
    transition: 0.5s;  
}

input:focus {
    border: 0.5px solid #031C30 !important;    
}

input::placeholder {
    opacity: 70%;
}
  
button {
    background-color: #031C30;
    color: #fff;
    border: none;
    margin-top: 1%;
    cursor: pointer;
}
  
a {
    color: #031C30;
    cursor: pointer;
    text-decoration: underline;
}
  
.btn-primary {
    background-color: #031C30;
    border: none;
    color: #fff;
    font-family: "Quicksand";
}

label{
    font-size: 13px;
    font-family: "Quicksand";
    color: #031C30;
}
