@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  height: 6rem;
  display: flex;
  border-bottom: 1px solid #f3fbfb;
  align-items: center;
  background-color: #f3fbfb;
}

.header img{
  margin: 0% 3% 0% 6%;
}

.header .search-bar {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: #ffffff;
  border-radius: 10rem;
  width: 45vw;
  height: 1.8rem;
  padding: 7.5px 16px;
  box-shadow: 0 0.2em 7px 1px rgba(3, 28, 48, 0.2);
}

.header .search-bar.focused {
  box-shadow: none;
  border: 0.5px solid rgba(3, 28, 48, 0.5);
  outline: none
}

.header .search-bar input {
  font-size: 16px;
  padding: 0px 12px;
  background-color: transparent;
  color: #818283;
  z-index: 1;
  outline: none;
  border: 1px solid #ffffff; 
}

.header .search-bar input:focus {
  box-shadow: none;
  border: 0.5px solid #ffffff !important;
  outline: none;  
}

.header .search-bar input::placeholder {
  color: #818283;
  opacity: 50%;
  position: absolute;
  outline: none;
  border: 1px white;
}

.header .search-bar .search-icon {
  color: rgba(3, 28, 48, 0.6);
  padding: 0px;
}

.header .search-bar button {
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  justify-content: flex-end;
  width: 16px;
  height: 16px;
  margin: 0;
  padding: 0px 16px 0px 16px;
}

.header .search-bar .clear-icon,
.header .search-bar .filter-icon,
.header .search-bar .rewrite-icon,
.header .search-bar .arrow-icon{
color: rgba(3, 28, 48, 0.6);
font-size: 16px;
}

.header .search-bar .clear-icon:hover,
.header .search-bar .filter-icon:hover,
.header .search-bar .rewrite-icon:hover,
.header .search-bar .arrow-icon:hover{
color: rgba(3, 28, 48, 1);
}

.header .search-bar .separation{
  display: block;
  border-left: 1px solid rgba(3,28,48,0.6);
  height: 70%;
  width: 1px;
  margin: 0px 0px 0px 13px;
}

.navbar {
  position: fixed;
  top: -4rem;
  width: 100%;
  z-index: 1000;
  background-color: #2a2a2b;
  transition: transform 0.3s ease-in-out;
  height: 4rem;
}

.navbar.show {
  transform: translateY(4rem);
}

.navbar img {
  margin: 0% 1.5% 0% 5%;
}

.navbar .search-bar {
  margin: 0%;
  padding: 5px 16px;
  background-color: #2a2a2b;
  border: 1px solid #3c4043;
}

.navbar .search-bar .search-icon,
.navbar .search-bar .clear-icon,
.navbar .search-bar .separation{
  visibility: hidden;
}

.navbar .search-bar input{
  font-size: 15px;
}

.navbar .search-bar .clear-icon,
.navbar .search-bar .arrow-icon{
  font-size: 15px;
}
.loading-screen {
    background-color: aliceblue;
    opacity: 80%;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-content: center;
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 0;
    left: 0;
    color: #031C30;
}
.Auth-form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    min-height:100%;
    overflow: hidden;
  }
  
.Auth-form {
    width: 28vw;
    box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
    padding: 30px 8px 30px 8px;
    border-radius: 8px;
    background-color: white;
    border: 0.1px solid #ccc;
    &.signup {
        height: 20.75em;
      }
    &.signin {
        height: 30vw;
    }
}
  
.Auth-form-content {
    margin: 0% 10%;
    padding: 0%;
    height: 100%;
}
  
.Auth-form-title {
    text-align: center;
    font-size: 24px;
    color: #031C30;
    font-weight: 800;
    padding: 0%;
    margin: 0%;
}
  
.text-center{
    font-family: 'Quicksand';
    font-size: 14px;
    text-align: center;
    padding: 0%;
    margin: 0% 0% 8% 0%;
}

.mt-2{
    padding-top: 15px;
    margin: 0%;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
}
  
.link-primary {
    color: #031C30;
    cursor: pointer;
    font-weight: bold;
}
  
.form-group {
    margin-bottom: 15px;
}

input, button {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    box-sizing: border-box; /* Include padding and border in the width calculation */
}
  
input {
    outline: none;
    border: 1px solid #ccc;
    transition: 0.5s;  
}

input:focus {
    border: 0.5px solid #031C30 !important;    
}

input::placeholder {
    opacity: 70%;
}
  
button {
    background-color: #031C30;
    color: #fff;
    border: none;
    margin-top: 1%;
    cursor: pointer;
}
  
a {
    color: #031C30;
    cursor: pointer;
    text-decoration: underline;
}
  
.btn-primary {
    background-color: #031C30;
    border: none;
    color: #fff;
    font-family: "Quicksand";
}

label{
    font-size: 13px;
    font-family: "Quicksand";
    color: #031C30;
}

/* App.css */

html, body {
