}

.App .header .search-bar button {
  border: None;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

.App .header .search-bar .clear-icon,
.App .header .search-bar .filter-icon,
.App .header .search-bar .rewrite-icon,
.App .header .search-bar .arrow-icon{
  color: #888;
  font-size: 16px;
}

.App .header .search-bar .clear-icon:hover,
.App .header .search-bar .arrow-icon:hover{
  color: #8ab4f8;
}

.App .header .search-bar .separation{
  display: block;
  border-left: 1px solid #888;
  height: 65%;
  margin: 0px 5px;
}

.App .navbar {
  position: fixed;
  top: -4rem;
  width: 100%;
  z-index: 1000;
  background-color: #2a2a2b;
  transition: transform 0.3s ease-in-out;
  height: 4rem;
}

.App .navbar.show {
  transform: translateY(4rem);
}

.App .navbar img {
  margin: 0% 1.5% 0% 5%;
}

.App .navbar .search-bar {
  margin: 0%;
  padding: 5px 16px;
  background-color: #2a2a2b;
  border: 1px solid #3c4043;
}

.App .navbar .search-bar .search-icon,
.App .navbar .search-bar .clear-icon,
.App .navbar .search-bar .separation{
  visibility: hidden;
}

.App .navbar .search-bar input{
  font-size: 15px;
}

.App .navbar .search-bar .clear-icon,
.App .navbar .search-bar .arrow-icon{
  font-size: 15px;
}

.App .main{
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-left: 13.5%;
  margin-right: 38.3%;
}

.App .main .results {
  padding: 0;
}

.App .main .results ul {
  padding: 0;
  margin: 0;
}

.App .main .results ul li {
  list-style-type: none;
  margin: 10px 0;
  font-size: 16px;
  line-height: 1.4;
}

.App .main .results ul li #r1{
  width: 30vw;
  height: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #bdc1c6;
  display: flex;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  align-items: center;
  justify-items: start;
}

.App .main .results ul li #r1 .iconContainer{
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.App .main .results ul li #r1 .iconContainer img{
  width: 20px;
  height: 20px;
  border: 2px solid white;
  background-color: white;
  border-radius: 50%;
}

.App .main .results ul li #r1 .textContainer{
  display: flex;
  flex-direction: column;
}

.App .main .results ul li #r1 .textContainer .site{
  color: #dadce0;
  text-decoration: none;
  font-size: 16px;
  line-height: 20px;
}

.App .main .results ul li #r1 .textContainer .link{
  color: #bdc1c6;
  text-decoration: none;
  font-size: 12px;
}

.App .main .results ul li .r2{
  width: 45vw;
  height: 4%;
  color: #8ab4f8;
  margin-bottom: 5%;
}

.App .main .results ul li .r2 a {
  color: #8ab4f8;
  font-size: 21px;
  margin-top: 18px;
  padding-bottom: 5px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  line-height: 1.272;
}

.App .main .results ul li .r2 p {
  color: #bdc1c6;
  font-size: 14px;
  margin: 0;
  padding-top: 5px;
}

.App .main .footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 45vw;
  margin: 20px 0px;
  padding: 10px;
}

.App .main .footer span{
  font: 'Quicksand';
  font-size: 30px;
  letter-spacing: 2px;
  color: #ffffff;
}

.App .main .footer .pagination {
  display: inline-block;
}

.App .main .footer .pagination button {
  color: white;
  font-size: 13px;
  float: left;
  padding: 5px 8px;
  text-decoration: none;
  border: none;
  background-color: transparent;
}

.App .main .footer .pagination button.active {
  color: #8ab4f8;
}

.App .main .footer .pagination button:hover:not(.active){
  color: #aaa9a9;
  cursor: pointer;
  text-decoration: underline;
}

.App .main .recents-panel {
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
  height: 100%;
  background-color:#031C30;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  padding: 20px;
  z-index: 1001;
  display: flex;
  flex-direction: column;
}

.App .main .recents-panel h3 {
  color: #fff;
  font-size: 18px;
  margin-bottom: 10px;
}

.App .main .recents-panel h4 {
  color: #fff;
  font-size: 0.75rem;
  margin-bottom: 10px;
}

.App .main .recents-panel ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.App .main .recents-panel li {
  cursor: pointer;
  padding: 10px;
  color: #fff; /*#bdc1c6;*/
  transition: background-color 0.3s ease;
}

.App .main .recents-panel li:hover {
  background-color: #353434;
  opacity: 50%;
}

